<template>
  <div class="throw-box">
    <throw-header :address="address"></throw-header>
    <div class="bottom-box">
      <div class="bottom-title">{{$t('nts.参投USDT将获得', {num: num})}}</div>
      <div class="bottom-content">
        <span class="num">10000</span>
        <span class="name">NTS!</span>
      </div>
      <div class="bottom-btn">
        <van-button type="primary" @click="$router.push({name: 'ThrowDetail'})" v-if="isInvest" size="large" >{{$t('nts.查看参投详情')}}</van-button>
        <van-button type="primary" v-else-if="Number(allowance)<=0" :loading="loading" @click="approve" :loading-text="$t('nts.授权中...')" size="large">{{$t('nts.授权USDT')}}</van-button>
        <van-button type="primary" v-else :loading="loading" :loading-text="$t('nts.参投中...')" @click="invest" size="large" >参投</van-button>
      </div>
      <div class="check-box">
        <van-checkbox label-disabled v-model="checked" checked-color="#9752C7"><span class="check-text" @click="showAgreement = true">{{$t('nts.NftList参投告知说明')}}</span></van-checkbox>
      </div>
    </div>
    <template v-if="locale == 'zh-cn'">
      <van-popup round closeable close-icon="close" v-model:show="showAgreement" :style="{ height: '90%', width: '96%' }" >
        <app-agreement></app-agreement>
      </van-popup>
    </template>
    <template v-if="locale == 'en'">
      <van-popup round closeable close-icon="close" v-model:show="showAgreement" :style="{ height: '90%', width: '96%' }" >
        <app-agreement-en></app-agreement-en>
      </van-popup>
    </template>
  </div>
</template>

<script>
import ThrowHeader from './ThrowHeader'
import AppAgreement from '@/components/AppAgreement' 
import AppAgreementEn from '@/components/AppAgreementEn' 
import { reactive, toRefs, computed, watch } from "vue";
import { useI18n } from "vue-i18n";
import {useStore} from "vuex";
import config from "@/utils/config";
import tokenAbi from "@/utils/abi";
import investAbi from "@/utils/investAbi";
import Clipboard from 'clipboard';
import { Toast } from 'vant';
import { MaxUint256, AddressZero } from "@ethersproject/constants";
import { intro } from '@/storage'
import { useRouter } from 'vue-router'
export default {
  components: {
    ThrowHeader,
    AppAgreement,
    AppAgreementEn
  },
  setup() {
    var clipboard = new Clipboard('.copy')
    clipboard.on('success', function (e) {
      Toast(t('share.复制成功'))
      e.clearSelection()
    })
    clipboard.on('error', function () {
      Toast(t('share.复制失败，请手动长按复制'))
    }) 
    const store = useStore()
    const { t } = useI18n()
    const router = useRouter()
    const data = reactive({
      $t: t,
      showAgreement: false,
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      investContract: null,
      usdtContract: null,
      allowance: '',
      checked: true,
      loading: false,
      isInvest: false,
      intro: intro.get() || config.rootAddress,
      num: 0,
      locale: computed(() => store.state.locale),
      initContract: () => {
        data.investContract = new data.web3.eth.Contract(investAbi, config.investAddress)
        data.usdtContract = new data.web3.eth.Contract(tokenAbi, config.usdtAddress)
        data.initData()
      },
      initData: () => {
        data.getAllowance()
        data.getIntro()
        data.getInvestNumber()
      },
      getAllowance: async () => {
        let allowance = await data.usdtContract.methods.allowance(data.address, config.investAddress).call()
        data.allowance = allowance;
      },
      getInvestNumber: async () => {
         let decimals = await data.usdtContract.methods.decimals().call()
         let num = await data.investContract.methods.usdtNum().call()
         data.num = Number(num/10**decimals).toFixed(2)
      },
      getIntro:async () => {
        let intro = await data.investContract.methods.getIntro(data.address).call()
        if(intro == AddressZero) {
          data.isInvest = false
        } else {
          data.isInvest = true
        }
      },
      approve: async () => {
        data.loading = true
        try {
          await data.usdtContract.methods.approve(config.investAddress, MaxUint256).send({from: data.address})
          Toast(t('nts.授权成功'))
          data.getAllowance()
          data.loading = false
        } catch (error) {
          Toast(t('nts.授权失败'))
          data.loading = false
        }  
      },
      invest: async () => {
        if(!data.checked) {
          Toast(t('nts.请先同意协议'))
          return
        }
        let balance = await data.usdtContract.methods.balanceOf(data.address).call()
        let num = await data.investContract.methods.usdtNum().call()
        if(Number(num) > Number(balance)) {
          Toast(t('nts.余额不足'))
          return
        }
        let intro = await data.investContract.methods.getIntro(data.intro).call()
        if(intro == AddressZero && data.intro != config.rootAddress) {
          Toast(t('nts.推荐人无效'))
          return
        }
        data.loading = true
        try {
          let gas = await data.investContract.methods.raiseUsdt(data.intro).estimateGas({from: data.address})
          await data.investContract.methods.raiseUsdt(data.intro).send({from: data.address, gas: gas})
          Toast(t('nts.参投成功'))
          router.push({name: 'ThrowDetail'})
          data.loading = false
          data.initData()
        } catch (error) {
          data.loading = false
          Toast(t('nts.质押失败'))
        }
        
      },
      timeEnd: ()=>{}
    })
    if (data.address) {
      data.initContract()
    }
     watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less" scoped>
 .throw-box{
   padding-bottom: 30px;
 }
  .bottom-box {
    text-align: center;
    margin: 0 15px;
    overflow: hidden;
    .bottom-title {
      font-size: 18px;
      font-weight: bold;
      color: #151515;
      margin-top: 38px;
    }
    .bottom-content {
      width: 100%;
      line-height: 64px;
      margin-top: 24px;
      border: 1px dashed #9752C7;
      font-size: 34px;
      font-weight: bold;
      background: #fff;
      border-radius: 32px;
      .num {
        color: #9752C7;
      }
      .name {
        color: #151515;
      }
    }
    .bottom-btn {
      margin-top: 38px;
    }
    .check-box {
      display: flex;
      align-items: center;
      justify-content: center;
      margin-top: 35px;
      .check-text {
        
        font-size: 14px;
        font-weight: bold;
        color: #151515;
      }
    }
  }
</style>